import React from "react"
import { navigate } from "gatsby"


function truncateString(str) {
  var num = 1000;
  if (str.length <= num) {
    return str
  }
  return str.slice(0, num) + '...'
}
const Post = ({  url,description,title }) => (
  <div class="post fade" onClick={ () => navigate(`posts/${url}`)} onKeyDown={() => navigate(`posts/${url}`)} role="link" tabIndex="0">
  <div>
  <div class="container blurred" style={{paddingBottom:`50px`}}>
    <h4 style={{ fontSize: "20px"}}>{title}</h4>
    <p style={{ fontSize: "17px"}}>{truncateString(description)}</p> 
  </div>
  </div>
  
  </div>
)


export default Post
