import React from "react"
import { navigate } from "gatsby"

const Card = ({  title,urlImg,id }) => (
  <div class="card" onClick={ () => navigate(`products/product${id}`)} onKeyDown={() => navigate(`products/product${id}`)} role="link" tabIndex="0">
    <div>
  <img src={urlImg} alt="Avatar"></img>
  <div class="container">
    <h4 class="cardTitle">{title}</h4> 
  </div>
  </div>
  </div>
)


export default Card
